import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/page/index'
import PcIndex from '@/page/pcIndex'
import PcAbout from '@/page/pcAbout'
import PhoneIndex from '@/page/phoneIndex'
import PhoneAbout from '@/page/phoneAbout'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/pcIndex',
      name: 'pcIndex',
      component: PcIndex
    },
    {
      path: '/pcAbout',
      name: 'pcAbout',
      component: PcAbout
    },
    {
      path: '/phoneIndex',
      name: 'phoneIndex',
      component: PhoneIndex
    },
    {
      path: '/phoneAbout',
      name: 'phoneAbout',
      component: PhoneAbout
    }
  ]
})



