    width:96px;
    height: 40px;
    background-color: #fff;
    /*color: #2468b1;*/
    margin-top: 18px;
    text-align: center;
    line-height: 40px;
    border-radius: 30px;
    font-size: 18px;
    
    position: relative;
    margin: 18px 20px 0 20px;
    
    
    
    &::after{
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: #ff8534;
      -webkit-transform: scaleX(0);
      transform: scaleX(0);
      -webkit-transform-origin: center;
      transform-origin: center;
      -webkit-transition: -webkit-transform 0.3s ease-in-out;
      transition: -webkit-transform 0.3s ease-in-out;
      transition: transform 0.3s ease-in-out;
      transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    }
  }
	.litwo{
  	margin: 0;
  	width: 96px;
  	height: 38px;
  	border: 1px solid #2468b1;
  	line-height: 40px;
  	text-align: center;
  	margin-top: 18px;
  	font-size: 16px;
  	color: #2468b1;
  }
  .lione{
  	margin: 0 20px;
  	width: 98px;
  	height: 40px;
  	background-color: #2989f0;
  	/*border: 1px solid #2468b1;*/
  	line-height: 40px;
  	text-align: center;
  	margin-top: 18px;
  	font-size: 16px;
  	color: #fff;
  }
  .yellow {
    color: #2468b1;
  }
</style>



