        <p>为装企赋能</p>
        <p>大幅提升装企运营管理营销效率</p>
      </div>
    </div>
    <section>
      <div class="title">
        <p class="content">精准管理决策有道</p>
        <p class="desc">降本增效运营更轻松</p>
      </div>
      <div class="img">
