        <p class="desc">提高客户转化率</p>
      </div>
      <div class="img">
        <div><img src="../assets/3-2.png"/></div>
      </div>
    </section>
    <section>
      <div class="title">
        <p class="content">全套跟进工具</p>
        <p class="desc">销售转化高效可控</p>
      </div>
      <div class="img">
        <div><img src="../assets/3-3.png"/></div>
      </div>
    </section>
    <div class="info">
      <div class="text">互联网式迭代 产品升级免费</div>
      <div class="text">365天不间断客服支持</div>
      <div class="text">国内一流技术团队</div>
      <div class="contact">
        <p class="tel">咨询电话:<a href="tel:18971481029">18971481029</a></p>
