        right: 0rem;
      }
    }
    .activeNav {
      background-color: rgba(0, 0, 0, 0.4);
      display: block;
    }
  }
  .banner {
    width: 100%;
    position: relative;
    height: 15rem;
    background-image: url(../assets/banner2.jpg);
    .text {
      position: absolute;
      background: rgba(0, 0, 0, 0.4);
